import { DialogComboboxSectionHeader } from '../DialogCombobox';

export interface SelectV2OptionGroupProps extends React.HTMLAttributes<HTMLDivElement> {
  name: string | React.ReactNode;
  children: React.ReactNode;
}

export const SelectV2OptionGroup = (props: SelectV2OptionGroupProps) => {
  const { name, children, ...restProps } = props;
  return (
    <>
      <DialogComboboxSectionHeader {...restProps}>{name}</DialogComboboxSectionHeader>
      {children}
    </>
  );
};
